import { Component } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';
import { DetailsPage } from '../details/details';
import { FilterModalPage } from '../filter-modal/filter-modal';
import { TodoService } from '../../providers/todo-service/todo-service';

interface Hotel{
    imageUrl: string,
    title: string,
    description: string,
    roomCost: number,
    hasParking: boolean,
    address: string,
    phone: string
} 

@Component({
  selector: 'page-hotels',
  templateUrl: 'hotels.html'
})
export class HotelsPage {
  private hotels: Hotel;

  // public parking: boolean;

  constructor(private todoService: TodoService, private modalController: ModalController, public navCtrl: NavController) {
       // this.hotels = this.todoService.getHotels();
    
  }
  public openDetalils(hotel){
    this.navCtrl.push(DetailsPage, hotel);
    console.log(hotel);
  }

  public openFilterModal(){
    let openFilterModal = this.modalController.create(FilterModalPage);
    openFilterModal.onDidDismiss((filterState)=>{
            this.todoService.getHotels().subscribe((hotels)=>{
             let zhotels = hotels;

             if (filterState.parking === true) {
              console.log(filterState);
              this.hotels = zhotels.filter((hotel)=>{
                    return hotel.hasParking === true && hotel.roomCost>filterState.minPrice && hotel.roomCost<filterState.maxPrice;
                  });
              }
              else { 
               console.log(filterState);
              this.todoService.getHotels().subscribe((response) =>{this.hotels = response.filter((hotel)=>{
                    return hotel.roomCost>filterState.minPrice && hotel.roomCost<filterState.maxPrice;
                  })
                }); 
            }
      })
    }
      // {console.log(filterState);} 
    );
      
    openFilterModal.present();
  }

    ionViewDidLoad(){
    //fires all internal setup
    //one time heavy liftin
    //fires only onces
    this.todoService.getHotels().subscribe((response) =>{this.hotels = response});
    
     }


}



